body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header-container{
    background-color: #FE9F6D;
    box-shadow: 3px 5px 4px #FFE8DC;
}
#logo{
    color:#341809;
    font-weight:bolder;
    letter-spacing: 3px;
    text-decoration: none
}

#nav-item{
    color:#341809;
    font-weight:bold;
    text-decoration: none;
    padding:20px;
}

.footer-container{
    background-color: #341809;
}

.brand{
    color:#FE9F6D
}
.main-container{
    background-color: white;
    min-height: 600px;
    margin-top:0.7%;
}
.card-container{
    background-color: white;
    box-shadow: 0 0 5px 2px #FE9F6D;
    margin: 20px;
    padding:20px;
}

